<template>
	<div class="nav">
		<van-nav-bar :title="navTitle">
			<template #left>
				<van-icon name="wap-nav" size="26" @click="showPopup" />
			</template>
		</van-nav-bar>

		<van-popup v-model="isShowPopup" position="left" :style="{ height: '100%',width:'60%' }">
			<div class="" v-for='(item,i) in navList' :key='i'>
				<van-collapse v-model="activeName" accordion v-if='item.areaList'>
					<van-collapse-item :title="item.title" name="1">
						<van-cell-group>
							<van-cell v-for='(areaItem,j) in item.areaList' :key='j' :title="areaItem.name" is-link :title-class='{"areaHover":areaId==areaItem.id}'
							 replace @click.stop="chooseArea(item,areaItem.id,j)">
							</van-cell>
						</van-cell-group>
					</van-collapse-item>
				</van-collapse>

				<van-cell-group v-else>
					<van-cell :title="item.title" is-link @click.stop="chooseArea(item)" replace>
					</van-cell>
				</van-cell-group>
			</div>
		</van-popup>
	</div>
</template>
<script>
	import {
		navList
	} from '@/api/json.js'
	import {
		getProjectAddress
	} from '@/api/module/index.js'
	export default {
		name: 'nav',
		props: {
			areaId: '',
			title: ''
		},
		data() {
			return {
				navList: [],
				// navTitle: "",
				isShowPopup: false,
				activeName: ["1"],

			}
		},
		computed:{
			navTitle(){
				return this.title
			}
		},
		created() {
			this.init()
		},
		methods: {
			init() {
				this.getData()
				// this.navTitle = this.title
			},
			showPopup() {
				this.isShowPopup = true
			},
			chooseArea(item, id,index) {
				this.navTitle = item.title
				if(item.title=='登出'){
					this.$store.commit(
					  "LOGOUT",
					  this.$store.state,
					);
					
					location.reload();
				}else{
					this.$set(this.$route.params, 'id', id)
					let title = id ?item.areaList[index].name+item.title: item.title
					let params = id ? {id,title} : {title}
					
					this.$emit('getAreaId', id)
					this.$emit('setTitle',title)
					this.navTitle  = title
					this.$router.replace({
						name: item.url,
						params
					}).catch(err => {
						console.log(err);
					})
				}
				this.isShowPopup = false
			},
			getData() {
				this.navList = navList;
				getProjectAddress().then(res => {
					if (res && res.success) {
						this.navList[0].areaList = res.result;
						this.$emit('setTitle', this.navList[0].areaList[0].name+this.navList[0].title)
					}
				}).catch(err => {
					console.log(err);
				});
			}

		}
	}
</script>
<style lang="scss">
	.nav {
		.van-collapse-item__title .van-cell__right-icon::before {
			-webkit-transform: rotate(0deg);
			transform: rotate(0deg);
		}

		.van-nav-bar {
			width: 100%;
			position: fixed;
			top: 0;
			left: 0;
			z-index: 999;
			background: #fff;
		}

		.areaHover {
			color: #1989fa;
		}
	}
</style>
