<template>
    <div >
        <van-tabbar v-model="active" class="tabbar" v-if="showTabbar" @change='changeTab'>
            <van-tabbar-item v-for="item in tabList" :key="item">
                <span>{{item.name}}</span>
                <template #icon="props">
                    <!-- <img :src="props.active ? item.activeIcon : item.icon" /> -->
                    <van-icon :name='item.icon'></van-icon>
                </template>
            </van-tabbar-item>
        </van-tabbar>
    </div>
</template>

<script>
export default {
    name:"tabbar",
    data() {
        return {
            active: 0,
            tabList:[
                {
                    name:"首页",
                    icon:"home-o",
                    activeIcon:"",
                    path:"GoodsIndex"
                },
                {
                    name:"我的",
                    icon:"friends-o",
                    activeIcon:"",
                    path:"UserIndex"
                },  
            ],
            // tabbar 白名单
            routeList:[
                'GoodsIndex',
                'UserIndex'
            ],
            showTabbar:false
        }
    },
    watch:{
        $route(to,from){
            const path=to.name
            this.footerChange(path)
        }    
    },
    methods:{
        //控制底边栏的显示隐藏
        footerChange(path){
            this.showTabbar = this.routeList.indexOf(path)>-1?true:false
        },
        changeTab(index){
            this.active = index
            this.$router.replace({
                name:this.tabList[index].path
            })
        }
    }

}
</script>

<style lang="scss" scoped>
    .tabbar{
        box-shadow: 0 0 1px rgba($color: #000000, $alpha: 0.2)
    }
</style>


