<template>
	<div>
		<Header :areaId='areaId' @getAreaId='getAreaId' @setTitle='setTitle' :title='navTitle'></Header>
		<Main :areaId='areaId'></Main>
	</div>
</template>

<script>
	import Header from "@/components/nav/nav.vue"
	import Main from "@/components/survey/survey.vue"
	export default {
		components: {
			Header,
			Main
		},
		data() {
			return {
				areaId: '',
				navTitle: ''
			}
		},
		mounted(){
			this.navTitle = this.$route.params.title?this.$route.params.title:"市场调研"
		},
		created() {
			
		},
		methods: {
			getAreaId(id) {
				this.areaId = id
				
			},
			setTitle(title){
				this.navTitle = title
			}
		}
	}
</script>
