<template>
	<div class="surveyMain">
		<van-form @submit="onSubmit" class='survey'>
			<van-field readonly clickable name="identifyConclusion" :value="identifyConclusionName" label="检测结论" placeholder="选择检测结论"
			 @click="showIdentifyConclusionPicker = true" :right-icon='showIdentifyConclusionPicker?"arrow-up":"arrow-down"'
			 size='16' class='stoneChoose'>
			</van-field>
			<van-popup v-model="showIdentifyConclusionPicker" position="bottom">
				<van-picker show-toolbar :columns="identifyConclusionList" value-key="name" :default-index="identifyConclusionIndex"
				 @confirm="onIdentifyConclusionConfirm" @cancel="showIdentifyConclusionPicker = false">
				</van-picker>
			</van-popup>

			<van-grid clickable :column-num="2">
				<!-- start 反馈值加默认值 -->
				<van-grid-item>
					<van-field readonly clickable name="category" :value="categoryName.val" label="品类" placeholder="请选择品类"
					 :right-icon='showCategoryPicker?"arrow-up":"arrow-down"' @click="showCategoryPicker = true">
					</van-field>
					<van-popup v-model="showCategoryPicker" position="bottom" round >
						<van-picker show-toolbar :columns="categoryList" value-key="name" @confirm="onCategoryConfirm"
						 @cancel="showCategoryPicker = false"
						 :defaultIndex='categoryName.index'
						 >
						</van-picker>
					</van-popup>
				</van-grid-item>
				<!-- end -->

				<van-grid-item v-if="this.identifyConclusionIdentifier=='FC'">
					<van-field readonly clickable name="qualityClassify" :value="qualityClassifyName" label="大级分类" placeholder="请选择大级分类"
					 :right-icon='showQualityClassifyPicker?"arrow-up":"arrow-down"' @click="showQualityClassifyPicker = true">
					</van-field>
					<van-popup v-model="showQualityClassifyPicker" position="bottom">
						<van-picker show-toolbar :columns="qualityClassifyList" value-key="name" @confirm="onQualityClassifyConfirm"
						 @cancel="showQualityClassifyPicker = false">
						</van-picker>
					</van-popup>
				</van-grid-item>
				<van-grid-item v-if="this.identifyConclusionIdentifier!='FC'">
					<van-field readonly clickable name="jadeType" :value="jadeTypeName" label="品种" placeholder="请选择品种" :right-icon='showJadeTypePicker?"arrow-up":"arrow-down"'
					 @click="showJadeTypePicker = true">
					</van-field>
					<van-popup v-model="showJadeTypePicker" position="bottom">
						<van-picker show-toolbar :columns="jadeTypeList" value-key="name" @confirm="onJadeTypeConfirm" @cancel="showJadeTypePicker = false">
						</van-picker>
					</van-popup>
				
				</van-grid-item>
				<van-grid-item v-if="this.identifyConclusionIdentifier!='FC'">
					<van-field readonly clickable name="color" :value="colorName" label="颜色" placeholder="请选择颜色" :right-icon='showColorPicker?"arrow-up":"arrow-down"'
					 @click="showColorPicker = true">
					</van-field>
					<van-popup v-model="showColorPicker" position="bottom">
						<van-picker show-toolbar :columns="colorList" value-key="name" @confirm="onColorConfirm" @cancel="showColorPicker = false">
						</van-picker>
					</van-popup>

				</van-grid-item>
				<van-grid-item v-if="this.identifyConclusionIdentifier!='FC'">
					<van-field readonly clickable name="texture" :value="textureName" label="质地" placeholder="请选择质地" :right-icon='showTexturePicker?"arrow-up":"arrow-down"'
					 @click="showTexturePicker = true">
					</van-field>
					<van-popup v-model="showTexturePicker" position="bottom">
						<van-picker show-toolbar :columns="textureList" value-key="name" @confirm="onTextureConfirm" @cancel="showTexturePicker = false">
						</van-picker>
					</van-popup>

				</van-grid-item>
				<van-grid-item>
					<van-field readonly clickable name="propertyStandard" :value="propertyStandardName" label="规格" placeholder="请选择规格"
					 :right-icon='showPropertyStandardPicker?"arrow-up":"arrow-down"' @click="showPropertyStandardPicker = true">
					</van-field>
					<van-popup v-model="showPropertyStandardPicker" position="bottom">
						<van-picker show-toolbar :columns="propertyStandardList" value-key="name" @confirm="onPropertyStandardConfirm"
						 @cancel="showPropertyStandardPicker = false">
						</van-picker>
					</van-popup>

				</van-grid-item>
				<van-grid-item>
					<van-field readonly clickable name="propertyCrafts" :value="propertyCraftsName" label="工艺" placeholder="请选择工艺"
					 :right-icon='showPropertyCraftsPicker?"arrow-up":"arrow-down"' @click="showPropertyCraftsPicker = true">
					</van-field>
					<van-popup v-model="showPropertyCraftsPicker" position="bottom">
						<van-picker show-toolbar :columns="propertyCraftsList" value-key="name" @confirm="onPropertyCraftsConfirm"
						 @cancel="showPropertyCraftsPicker = false">
						</van-picker>
					</van-popup>

				</van-grid-item>
				<van-grid-item v-if="this.identifyConclusionIdentifier!='FC'">
					<van-field readonly clickable name="gloss" :value="glossName" label="光泽" placeholder="请选择光泽" :right-icon='showGlossPicker?"arrow-up":"arrow-down"'
					 @click="showGlossPicker = true">
					</van-field>
					<van-popup v-model="showGlossPicker" position="bottom">
						<van-picker show-toolbar :columns="glossList" value-key="name" @confirm="onGlossConfirm" @cancel="showGlossPicker = false">
						</van-picker>
					</van-popup>

				</van-grid-item>
				<van-grid-item>
					<van-field readonly clickable name="propertyPurity" :value="propertyPurityName" label="净度" placeholder="请选择净度"
					 :right-icon='showPropertyPurityPicker?"arrow-up":"arrow-down"' @click="showPropertyPurityPicker = true">
					</van-field>
					<van-popup v-model="showPropertyPurityPicker" position="bottom">
						<van-picker show-toolbar :columns="propertyPurityList" value-key="name" @confirm="onPropertyPurityConfirm"
						 @cancel="showPropertyPurityPicker = false">
						</van-picker>
					</van-popup>

				</van-grid-item>
				<van-grid-item v-if="this.identifyConclusionIdentifier=='FC'">
					<van-field readonly clickable name="theme" :value="themeName" label="题材" placeholder="请选择题材" :right-icon='showThemePicker?"arrow-up":"arrow-down"'
					 @click="showThemePicker = true">
					</van-field>
					<van-popup v-model="showThemePicker" position="bottom">
						<van-picker show-toolbar :columns="themeList" value-key="name" @confirm="onThemeConfirm" @cancel="showThemePicker = false">
						</van-picker>
					</van-popup>

				</van-grid-item>
				<van-grid-item v-if="this.identifyConclusionIdentifier=='FC'">
					<van-field readonly clickable name="mainColorChroma" :value="mainColorChromaName" label="主色调彩度" placeholder="请选择主色调彩度"
					 :right-icon='showMainColorChromaPicker?"arrow-up":"arrow-down"' @click="showMainColorChromaPicker = true">
					</van-field>
					<van-popup v-model="showMainColorChromaPicker" position="bottom">
						<van-picker show-toolbar :columns="mainColorChromaList" value-key="name" @confirm="onMainColorChromaConfirm"
						 @cancel="showMainColorChromaPicker = false">
						</van-picker>
					</van-popup>

				</van-grid-item>
				<van-grid-item v-if="this.identifyConclusionIdentifier=='FC'">
					<van-field readonly clickable name="mainColor" :value="mainColorName" label="色调" placeholder="请选择色调" :right-icon='showMainColorPicker?"arrow-up":"arrow-down"'
					 @click="showMainColorPicker = true">
					</van-field>
					<van-popup v-model="showMainColorPicker" position="bottom">
						<van-picker show-toolbar :columns="mainColorList" value-key="name" @confirm="onMainColorConfirm" @cancel="showMainColorPicker = false">
						</van-picker>
					</van-popup>

				</van-grid-item>
				<van-grid-item v-if="this.identifyConclusionIdentifier=='FC'">
					<van-field readonly clickable name="mainColorProportion" :value="mainColorProportionName" label="主色调占比"
					 placeholder="请选择主色调占比" :right-icon='showMainColorProportionPicker?"arrow-up":"arrow-down"' @click="showMainColorProportionPicker = true">
					</van-field>
					<van-popup v-model="showMainColorProportionPicker" position="bottom">
						<van-picker show-toolbar :columns="mainColorProportionList" value-key="name" @confirm="onMainColorProportionConfirm"
						 @cancel="showMainColorProportionPicker = false">
						</van-picker>
					</van-popup>

				</van-grid-item>
				<van-grid-item>
					<van-field v-model="weight" type="number" label="重量" placeholder="请输入重量">
					</van-field>
				</van-grid-item>
				<van-grid-item>
					<van-field v-model="price" type="number" label="价格" placeholder="请输入价格">
					</van-field>
				</van-grid-item>
				<van-grid-item>
					<van-field v-model="remark" type="input" label="备注" placeholder="请输入备注">
					</van-field>
				</van-grid-item>
			</van-grid>

			<van-field name="uploader" label="文件上传">
				<template #input>
					<van-uploader v-model="uploader" :accept="'image/*'" multiple  :max-count="9"/>
				</template>
			</van-field>


			<van-field name="uploader" label="音频上传">
				<template #input>
					<van-uploader v-model="audioFile" :accept="'audio/*'" multiple  :max-count="9"/>
				</template>
			</van-field>

			<div style="margin: 32px 64px;">
				<van-button round block type="info" native-type="submit">
					提交
				</van-button>
			</div>
		</van-form>
	</div>
</template>
<script>
	import {
		getIdentifyConclusion,
		getCategory,
		getJadeType,
		getColor,
		getTexture,
		getPropertyStandard,
		getPropertyCrafts,
		getGloss,
		getPropertyPurity,
		getTheme,
		getMainColorChroma,
		getMainColor,
		getMainColorProportion,
		getQualityClassify,
		saveSurvey
	} from '@/api/module/index.js'
	import { Notify } from 'vant'
	export default {
		name: "SurveyMain",
		props: {
			areaId: ""
		},
		data() {
			return {
				identifyConclusionList: [],
				identifyConclusionVal: '',
				identifyConclusionName: '',
				identifyConclusionIdentifier: '',
				showIdentifyConclusionPicker: false,
				colorList: [],
				colorVal: '',
				colorName: '',
				showColorPicker: false,
				textureList: [],
				textureVal: '',
				textureName: '',
				showTexturePicker: false,
				propertyStandardList: [],
				propertyStandardVal: '',
				propertyStandardName: '',
				showPropertyStandardPicker: false,
				propertyCraftsList: [],
				propertyCraftsVal: '',
				propertyCraftsName: '',
				showPropertyCraftsPicker: false,
				glossList: [],
				glossVal: '',
				glossName: '',
				showGlossPicker: false,
				propertyPurityList: [],
				propertyPurityVal: '',
				propertyPurityName: '',
				showPropertyPurityPicker: false,
				themeList: [],
				themeVal: '',
				themeName: '',
				showThemePicker: false,
				mainColorChromaList: [],
				mainColorChromaVal: '',
				mainColorChromaName: '',
				showMainColorChromaPicker: false,
				mainColorList: [],
				mainColorVal: '',
				mainColorName: '',
				showMainColorPicker: false,
				mainColorProportionList: [],
				mainColorProportionVal: '',
				mainColorProportionName: '',
				showMainColorProportionPicker: false,
				qualityClassifyList: [],
				qualityClassifyVal: '',
				qualityClassifyName: '',
				showQualityClassifyPicker: false,
				categoryList: [],
				categoryVal: '',
				// categoryName: '',
				categoryName:{
					val:"",
					index:""
				},
				showCategoryPicker: false,
				jadeTypeList: [],
				jadeTypeVal: '',
				jadeTypeName: '',
				showJadeTypePicker: false,
				weight: '',
				price: '',
				remark: '',
				uploader: [],
				audioFile:[]
			}
		},
		created() {
			this.areaId = this.getUrlParameter("id");
			this.init();
		},
		methods: {
			getUrlParameter(name) {
				name = name.replace(/[]/, "\[").replace(/[]/, "\[").replace(/[]/, "\\\]");
				var regexS = "[\\?&]" + name + "=([^&#]*)";
				var regex = new RegExp(regexS);
				var results = regex.exec(window.parent.location.href);
				if (results == null) return "";
				else {
					return results[1];
				}
			},
			setDefaultVal(key,index,val){
				this.$nextTick(() => {
					this[key] = {val,index}
				})
			},
			init() {
				getIdentifyConclusion().then(res => {
					if (res && res.success) {
						this.identifyConclusionList = res.result;
					}
				}).catch(err => {
					console.log(err);
				});
				getCategory().then(res => {
					if (res && res.success) {
						this.categoryList = res.result;
						this.setDefaultVal('categoryName',2,res.result[2].name)
					}
				}).catch(err => {
					console.log(err);
				});
				getJadeType().then(res => {
					if (res && res.success) {
						this.jadeTypeList = res.result;
					}
				}).catch(err => {
					console.log(err);
				});
				getColor().then(res => {
					if (res && res.success) {
						this.colorList = res.result;
					}
				}).catch(err => {
					console.log(err);
				});
				getTexture().then(res => {
					if (res && res.success) {
						this.textureList = res.result;
					}
				}).catch(err => {
					console.log(err);
				});
				getPropertyStandard().then(res => {
					if (res && res.success) {
						this.propertyStandardList = res.result;
					}
				}).catch(err => {
					console.log(err);
				});
				getPropertyCrafts().then(res => {
					if (res && res.success) {
						this.propertyCraftsList = res.result;
					}
				}).catch(err => {
					console.log(err);
				});
				getGloss().then(res => {
					if (res && res.success) {
						this.glossList = res.result;
					}
				}).catch(err => {
					console.log(err);
				});
				getPropertyPurity().then(res => {
					if (res && res.success) {
						this.propertyPurityList = res.result;
					}
				}).catch(err => {
					console.log(err);
				});
				getTheme().then(res => {
					if (res && res.success) {
						this.themeList = res.result;
					}
				}).catch(err => {
					console.log(err);
				});
				getMainColorChroma().then(res => {
					if (res && res.success) {
						this.mainColorChromaList = res.result;
					}
				}).catch(err => {
					console.log(err);
				});
				getMainColor().then(res => {
					if (res && res.success) {
						this.mainColorList = res.result;
					}
				}).catch(err => {
					console.log(err);
				});
				getMainColorProportion().then(res => {
					if (res && res.success) {
						this.mainColorProportionList = res.result;
					}
				}).catch(err => {
					console.log(err);
				});
				getQualityClassify().then(res => {
					if (res && res.success) {
						this.qualityClassifyList = res.result;
					}
				}).catch(err => {
					console.log(err);
				});
			},
			onIdentifyConclusionConfirm(value, index) {
				if (value) {
					this.identifyConclusionVal = value.id;
					this.identifyConclusionName = value.name;
					this.identifyConclusionIdentifier = value.identifier;
					this.identifyConclusionIndex = index;
				}
				this.showIdentifyConclusionPicker = false;
			},
			onCategoryConfirm(value, index) {
				if (value) {
					this.categoryVal = value.id;
					this.categoryIndex = index;
					this.setDefaultVal('categoryName',index,val.name)
				}
				this.showCategoryPicker = false;
			},
			onJadeTypeConfirm(value, index) {
				if (value) {
					this.jadeTypeVal = value.id;
					this.jadeTypeName = value.name;
					this.jadeTypeIndex = index;
				}
				this.showJadeTypePicker = false;
			},
			onColorConfirm(value, index) {
				if (value) {
					this.colorVal = value.id;
					this.colorName = value.name;
					this.colorIndex = index;
				}
				this.showColorPicker = false;
			},
			onTextureConfirm(value, index) {
				if (value) {
					this.textureVal = value.id;
					this.textureName = value.name;
					this.textureIndex = index;
				}
				this.showTexturePicker = false;
			},
			onPropertyStandardConfirm(value, index) {
				if (value) {
					this.propertyStandardVal = value.id;
					this.propertyStandardName = value.name;
					this.propertyStandardIndex = index;
				}
				this.showPropertyStandardPicker = false;
			},
			onPropertyCraftsConfirm(value, index) {
				if (value) {
					this.propertyCraftsVal = value.id;
					this.propertyCraftsName = value.name;
					this.propertyCraftsIndex = index;
				}
				this.showPropertyCraftsPicker = false;
			},
			onGlossConfirm(value, index) {
				if (value) {
					this.glossVal = value.id;
					this.glossName = value.name;
					this.glossIndex = index;
				}
				this.showGlossPicker = false;
			},
			onPropertyPurityConfirm(value, index) {
				if (value) {
					this.propertyPurityVal = value.id;
					this.propertyPurityName = value.name;
					this.propertyPurityIndex = index;
				}
				this.showPropertyPurityPicker = false;
			},
			onThemeConfirm(value, index) {
				if (value) {
					this.themeVal = value.id;
					this.themeName = value.name;
					this.themeIndex = index;
				}
				this.showThemePicker = false;
			},
			onMainColorChromaConfirm(value, index) {
				if (value) {
					this.mainColorChromaVal = value.id;
					this.mainColorChromaName = value.name;
					this.mainColorChromaIndex = index;
				}
				this.showMainColorChromaPicker = false;
			},
			onMainColorConfirm(value, index) {
				if (value) {
					this.mainColorVal = value.id;
					this.mainColorName = value.name;
					this.mainColorIndex = index;
				}
				this.showMainColorPicker = false;
			},
			onMainColorProportionConfirm(value, index) {
				if (value) {
					this.mainColorProportionVal = value.id;
					this.mainColorProportionName = value.name;
					this.mainColorProportionIndex = index;
				}
				this.showMainColorProportionPicker = false;
			},
			onQualityClassifyConfirm(value, index) {
				if (value) {
					this.qualityClassifyVal = value.id;
					this.qualityClassifyName = value.name;
					this.qualityClassifyIndex = index;
				}
				this.showQualityClassifyPicker = false;
			},
			onSubmit() {
				if(!this.areaId){
					Notify({ background: 'rgba(0,0,0,0.4)', message: '请选择调研市场' });
					return;
				}
				let data = {
					"identifyConclusionIdentifier": this.identifyConclusionIdentifier,
					"identifyConclusion": this.identifyConclusionVal+'',
					"jadeType": this.jadeTypeVal+'',
					"category": this.categoryVal+'',
					"color": this.colorVal+'',
					"texture": this.textureVal+'',
					"propertyStandard": this.propertyStandardVal+'',
					"propertyCrafts": this.propertyCraftsVal+'',
					"gloss": this.glossVal+'',
					"propertyPurity": this.propertyPurityVal+'',
					"theme": this.themeVal+'',
					"mainColorChroma": this.mainColorChromaVal+'',
					"mainColor": this.mainColorVal+'',
					"mainColorProportion": this.mainColorProportionVal+'',
					"qualityClassify": this.qualityClassifyVal+'',
					"weight": this.weight+'',
					"price": this.price+'',
					"remark": this.remark,
					"projectAddress":this.areaId+'',
					"files":this.uploader
				};

				console.log( data)
				return
				saveSurvey(data).then(res => {
					if (res && res.success) {
						Notify({  background: 'rgba(7,193,96,0.4)', message: '保存数据成功' });
					}
				}).catch(err => {
					console.log(err);
				});
			}
		}
	}
</script>

<style lang="scss">
	.surveyMain {
		.van-field__label {
			width: 4.2em;
		}

		.survey .van-cell::after {
			border: none;
		}

		.van-form {
			margin-top: 46px;
		}

		.stoneChoose {
			width: 230px;
		}

		.stoneChoose .van-field__body {
			border-radius: 4px;
			border: 1px solid #ebedf0;
			padding: 2px 6px;
		}

		.stoneChoose .van-field__body::after {
			border-radius: 4px;
			border: 0 solid #ebedf0;
		}
	}
</style>
