import axios from 'axios'
import {
	Toast
} from 'vant'
import store from '../store/index'
import router from '../router'
import env from "@/utils/env.js"
const ConfigBaseURL = env.baseURL 
const toLogin = () => {
	router.replace({
		name: 'Login',
		params: {
			url: router.currentRoute.fullPath,
			params: router.currentRoute.params
		}
	})
}

let loadingInstance = null //这里是loading
export const Service = axios.create({
	timeout: 7000, // 请求超时时间
	baseURL: ConfigBaseURL,
})

// 添加请求拦截器
Service.interceptors.request
	.use(config => {
			const token = store.state.user.token
			token && (config.headers.Authorization = token)
			loadingInstance = Toast.loading({
				message: '加载中...',
				forbidClick: true,
			})
			return config
		},
		error => {
			return Promise.error(error)
		}
	)
// 添加响应拦截器
Service.interceptors.response.use(
	res => {
		const data = res.data;
		// 根据返回的code值来做不同的处理(和后端约定)
		switch (data.code) {
			case 401:
				// 未登录 清除已登录状态
				toLogin();
				break;
			case 10002:
				Toast({
					message: '登录过期，请重新登录',
					duration: 2000,
					forbidClick: true
				});
				//退出登录
				store.commit('LOGOUT');
				// 跳转登录页面
				setTimeout(() => {
					toLogin()
				}, 2000);
				break;
			case 403:
				// 没有权限
				if (data.message !== null) {
					Toast({
						message: data.message,
						duration: 2000,
						forbidClick: true
					});
				} else {
					Toast({
						message: '未知错误',
						duration: 2000,
						forbidClick: true
					});
				}
				break;
			case 500:
				// 错误
				if (data.message !== null) {
					Toast({
						message: data.message,
						duration: 2000,
						forbidClick: true
					});
				} else {
					Toast({
						message: '未知错误',
						duration: 2000,
						forbidClick: true
					});
				}
				break;
			default:
				return Promise.resolve(res);
		}
		loadingInstance.clear()
		return Promise.resolve(res);
	},
	error => {
		// 返回状态码不为200时候的错误处理
		Toast({
			message: error.toString(),
			duration: 2000,
			forbidClick: true
		});
		return Promise.resolve(error);
	})

/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */

export function get(url, params = {}) {
	return new Promise((resolve, reject) => {
		Service.get(url, {
				params: params
			})
			.then(response => {
				resolve(response.data)
			})
			.catch(err => {
				reject(err)
			})
	})
}


/**
 * 封装post请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function post(url, data = {}) {
	return new Promise((resolve, reject) => {
		Service.post(url, data)
			.then(response => {
				resolve(response.data)
			})
			.catch(err => {
				reject(err)
			})
	})
}
